import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	data() {
		return {
			type: '',
			menus:[
				/* {"id":"zhye","icon":"../../static/zhye.png","title":"账户余额","event":"zhye"}, */
				{"id":"wdyhq","icon":"../../static/wdyhq.png","title":"我的优惠券","event":"wdyhq"},
				{"id":"wdpj","icon":"../../static/wdpj.png","title":"我的评价","event":"wdpj"},
				{"id":"fxlq","icon":"../../static/fxlq.png","title":"分享领券","event":"fxlq"},
				{"id":"jygz","icon":"../../static/jygz.png","title":"交易规则","event":"jygz"},
				{"id":"mzsm","icon":"../../static/mzsm.png","title":"免责声明","event":"mzsm"},
				{"id":"lxpt","icon":"../../static/lxpt.png","title":"联系平台","event":"lxpt"}
			],
			avatarUrl:'',
			nickname:'',
			userinfo:'',
			user_id:0,
			user:[]
		}
	},
	components:{
		uniPopup
	},
	onLoad() {
		var _this = this;
		var wechar_userinfo = uni.getStorageSync("wx_userInfo");
		this.avatarUrl = wechar_userinfo.avatarUrl;
		this.nickname = wechar_userinfo.nickName;
		
		this.userinfo = uni.getStorageSync('userinfo');
		this.user_id  = this.userinfo.user_id;
		
	},
	onShow() {
		var _this = this;
		
		uni.request({
			url: this.serverURL + 'home/user/user_get_order',
			method:'POST',
			data:{
				id: _this.user_id
			},
			success(res) {
				_this.user = res.data;
			},
			fail(error) {
				console.log(error)
			}
		})
	},
	onShareAppMessage() {
		var _this = this;
		return {
			title: "如期货运",
			path: '/pages/index/index?uid='+_this.user_id,
			imageUrl:'../../static/share.png'
		}
	},
	methods: {
		clickMenu(type) {
			var _this = this;
			if(type=="lxpt"){
				_this.type = type;
				uni.hideTabBar();
			} else if(type==''){
				_this.type = type;
				uni.showTabBar()
			} else if(type=='jygz'){
				uni.navigateTo({
					url:"../TradingRules/TradingRules"
				})
			} else if(type=='wdpj'){
				uni.navigateTo({
					url:"../myAssess/myAssess"
				})
			} else if(type=='wdyhq'){
				uni.navigateTo({
					url:"../myCoupon/myCoupon"
				})
			} else if(type=='zhye'){
				uni.navigateTo({
					url:"../cashOut/cashOut"
				})
			} else if(type=='mzsm'){
				uni.navigateTo({
					url:"../statement/statement"
				})
			} else if(type=="zlxg"){
				uni.navigateTo({
					url:"../update/update"
				})
			}
		},
		makePhoneCall(e) {
			var phone = e.currentTarget.dataset.phone;
			console.log(phone);
			uni.makePhoneCall({
				phoneNumber: phone
			})
		},
		goTab(index){
			uni.switchTab({
				url:'../order/order?index='+index
			})
		}
	}
}
